<template>
  <div>
    <div class="search">
      <el-input v-model="title" placeholder="请输入内容"></el-input>
      <el-button type="primary" @click="getVideoList">搜索</el-button>
    </div>
    <div>
      <el-row :gutter="20">
        <el-col :span="8" v-for="(item, index) in videoList" :key="index">
          <div style="margin-bottom: 20px">
            <el-card :body-style="{ padding: '0px' }">
              <img :src="item.image" class="image" style="width: 500px;height: 400px">
              <div style="padding: 14px;">
                <el-link type="info" @click="open(item.url)">{{ item.title }}</el-link>
              </div>
              <div>
                <span>发布时间: {{ item.publishTime }}</span>
              </div>
            </el-card>
          </div>
        </el-col>
      </el-row>
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page"
          :page-sizes="[10, 50, 100, 300]"
          :page-size="size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>

import { getVideoList } from "@/api/user";

export default {
  name: "video-list",
  data() {
    return {
      visible: false,
      currentDate: new Date(),
      page: 1,
      size: 20,
      title: null,
      total: 0,
      videoList: [],
      params: {
        title: '',
        current: 1,
        size: 10
      }
    }
  },
  created() {
    this.getVideoList()
  },
  methods: {
    open(url) {
      window.open(url)
    },
    getVideoList() {
      let formData = new FormData();
      formData.append('current', this.page);
      formData.append('size', this.size);
      if (this.title !== null) {
        formData.append('title', this.title)
      }
      // 发送 POST 请求
      getVideoList(formData).then(res => {
        this.videoList = res.data.content
        this.total = res.data.totalElements
      })
    },
    handleSizeChange(val) {
      this.size = val
      document.documentElement.scrollTop = 0;
      this.getVideoList()
    },
    handleCurrentChange(val) {
      document.documentElement.scrollTop = 0;
      this.page = val
      this.getVideoList()
    }
  }
}
</script>

<style scoped>
html {
  padding: 0;
  bottom: 0;
}

.search {
  display: flex;
  margin-left: 800px;

  width: 400px;
  margin-top: 100px;
  margin-bottom: 200px;
}
</style>